body {
  background: #fff;
  font-family: Ubuntu, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.noscroll {
  position: fixed;
  overflow: hidden;
}

img {
  max-width: 100%;
  display: block;
}

body, p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.hidden {
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.container {
  width: 1280px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container {
    width: 100%;
    max-width: max-content;
  }
}

header {
  margin: 0;
  padding: 20px 0 0;
}

.header-wrap {
  text-align: left;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header-wrap {
    flex-flow: column wrap;
  }
}

.header-logo {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.header-logo img {
  max-width: none;
}

.header-logo p {
  width: 234px;
  color: #0a0a0a;
  padding: 20px;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.15;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header-logo {
    text-align: center;
    flex-direction: column;
    justify-content: center;
  }

  .header-logo p {
    padding: 20px 0;
    font-size: 16px;
  }

  .header-logo img {
    max-width: 100%;
    width: 150px;
    height: auto;
  }
}

.header-map {
  width: 370px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-map img {
  max-width: none;
}

.header-map p {
  padding: 20px;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 1.2;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header-map {
    justify-content: center;
  }

  .header-map img {
    display: none;
  }

  .header-map p {
    text-align: center;
    padding: 20px 0;
  }
}

.header-contacts {
  justify-content: flex-start;
  align-items: center;
  display: flex;
}

.header-contacts img {
  max-width: none;
}

.header-contacts p {
  flex-wrap: wrap;
  padding: 20px;
  display: flex;
}

.header-contacts a:nth-child(1) {
  color: #0a0a0a;
  font-size: 22px;
  font-weight: 300;
  text-decoration: none;
}

.header-contacts a:nth-child(2) {
  color: #126c03;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header-contacts {
    justify-content: center;
  }

  .header-contacts img {
    width: 70px;
    padding-right: 20px;
  }

  .header-contacts p {
    flex-flow: column wrap;
    padding: 20px 0;
    display: flex;
  }
}

.header-menu ul {
  width: 1280px;
  height: auto;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 auto;
  padding: 20px 0;
  display: flex;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .header-menu ul {
    display: none;
  }
}

.header-menu ul, .header-menu ul li {
  list-style-type: none;
}

.header-menu ul li {
  margin-right: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.header-menu ul li:hover, .header-menu ul li:focus {
  transform: scale(1.3);
}

.header-menu ul li:not(:last-child) {
  margin-right: 40px;
}

.header-menu ul li a {
  color: #0a0a0a;
  font-size: 18px;
  font-weight: 300;
  text-decoration: none;
}

.header-menu ul li ul li a {
  font-size: 14px;
}

.header-menu ul li ul {
  max-width: 250px;
  visibility: hidden;
  opacity: 0;
  background-color: #fff;
  flex-wrap: wrap;
  transition: opacity .2s linear;
  display: flex;
  position: absolute;
  top: 20px;
  left: -20px;
}

.header-menu ul li:hover ul {
  visibility: visible;
  opacity: 1;
  transition: opacity .2s linear;
}

.header-menu ul li ul li {
  min-width: 250px;
  padding: 5px 20px;
  transition: none;
}

.header-menu ul li ul li:hover, .header-menu ul li ul li:focus {
  transform: scale(1);
}

.header-menu ul li ul li a:hover {
  color: #ffd952;
}

.header-menu ul li ul li .header-menu ul li ul li a {
  font-size: 14px;
}

.header-menu ul li a .fa-angle-down {
  vertical-align: middle;
  transition: all .5s ease-in-out;
}

.header-menu ul li:hover a .fa-angle-down {
  transition: all .5s ease-in-out;
  transform: rotate(-90deg);
}

.three, .mobile-menu {
  display: none;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .three, .mobile-menu {
    display: block;
  }
}

.visible {
  display: block !important;
}

.mobile-menu {
  height: 100vh;
  width: 100%;
  background-color: #111;
  transition: all .5s;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.mobile-menu .container-mobile .menu-container {
  width: 250px;
  height: 100vh;
  z-index: 99;
  background-color: #e17992;
  flex-direction: column;
  padding: 60px 0;
  transition: transform .35s cubic-bezier(.8, .4, .3, .9);
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.mobile-menu ul {
  padding: 50px 30px;
}

.mobile-menu ul li {
  margin: 15px 0;
  list-style-type: none;
  position: relative;
}

.mobile-menu ul li a {
  color: #efefef;
  font-size: 22px;
  text-decoration: none;
}

.mobile-menu ul li ul {
  margin: 0;
  padding: 0 30px;
}

.mobile-menu ul li div.click {
  color: #fff;
  font-size: 20px;
  position: absolute;
  top: 0;
  left: 220px;
}

.container-mobile ul ul li a {
  font-size: 18px;
}

.mobile-menu ul ul {
  display: none;
}

.hamburger {
  z-index: 1001;
  background-color: #111;
  padding: 5px;
  position: fixed;
  top: 20px;
  right: 20px;
}

.hamburger:hover {
  cursor: pointer;
}

.hamburger .line {
  width: 30px;
  height: 5px;
  -o-transition: all .3s ease-in-out;
  background-color: #fff;
  margin: 5px;
  transition: all .3s ease-in-out;
  display: block;
}

#hamburger-1.is-active .line:nth-child(2) {
  opacity: 0;
}

#hamburger-1.is-active .line:nth-child(1) {
  transform: translateY(10px)rotate(45deg);
}

#hamburger-1.is-active .line:nth-child(3) {
  transform: translateY(-10px)rotate(-45deg);
}

