.repair {
  min-height: 600px;
  background-color: $secondary-pages-color;
  .repair-list {
    ol {
      display: flex;
      flex-wrap: nowrap;
      align-items: center;
      justify-items: center;
      justify-content: space-between;

      margin: 0;
      list-style: none;
      border-bottom: 4px solid #ffd059;
      counter-reset: timeline;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        flex-wrap: wrap;
        padding: 50px 0;
      }
      @media screen and (min-width: 768px) {
      }
      @media screen and (min-width: 1280px) {
      }
    }

    ol li {
      margin-left: -16px;
      margin: 0;
      padding: 40px 20px 40px 36px;
      position: relative;

      &:before,
      &:after {
        position: absolute;
        top: 0;
        bottom: 0;
        content: '';
      }

      &:before {
        height: 60px;
        width: 60px;
        background: #fff;
        left: -30px;
        border: 1px solid #ccc;
        border-radius: 100%;
        margin: auto;
      }

      &:after {
        height: 50px;
        width: 50px;
        left: -25px;
        margin: auto;
        border-radius: 100%;
        background: #ddd;
        counter-increment: timeline;
        content: counter(timeline);
        text-align: center;
        line-height: 50px;
        transform: scale(1);
        transform-origin: center center;
        box-shadow: 0 0 0 0px blue;
        transition: transform 0.2s ease-in-out,
          box-shadow 0.15s 0.2s ease-in-out;
      }

      &:hover:after {
        transform: scale(1.1);
        box-shadow: 0 0 0 4px blue;
      }

      .border-left {
        position: absolute;
        width: 4px;
        height: 70%;
        top: 50%;
        transform: translateY(-50%);
        background: #ffd059;
        border-bottom-left-radius: 50px;
      }

      @media screen and (min-width: 320px) and (max-width: 767px) {
        max-width: 50%;
        width: 50%;
        padding: 0 0 20px 0;
        word-break: keep-all;
        &:last-child {
          margin: 10px auto;
        }
      }
      @media screen and (min-width: 768px) {
      }
      @media screen and (min-width: 1280px) {
      }
      p {
        width: 100%;
        color: $main-text-color;
        line-height: 1.4;
        font-size: 16px;
        font-weight: 300;
        text-align: center;
        padding: 10px 20px;

        @media screen and (min-width: 320px) and (max-width: 767px) {
          width: 100%;
          text-transform: none;
          padding: 10px;
        }
        @media screen and (min-width: 768px) {
        }
        @media screen and (min-width: 1280px) {
        }
      }
      img {
        margin: 0 auto;
      }
    }
    h2 {
      width: 800px;
      color: $main-text-color;
      font-size: 45px;
      line-height: 1.15;
      font-weight: 400;
      margin-bottom: 50px;
      padding-top: 30px;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        width: 100%;
        text-align: center;
      }
      @media screen and (min-width: 768px) {
      }
      @media screen and (min-width: 1280px) {
      }
    }
  }
  .repair-materials {
    padding-bottom: 50px;
    h2 {
      width: 800px;
      color: $main-text-color;
      font-size: 45px;
      line-height: 1.15;
      font-weight: 400;
      margin-bottom: 50px;
      padding-top: 30px;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        width: 100%;
        text-align: center;
      }
      @media screen and (min-width: 768px) {
      }
      @media screen and (min-width: 1280px) {
      }
    }
    p {
      width: 100%;
      color: $main-text-color;
      font-size: 20px;
      line-height: 1.55;
      font-weight: 300;
      text-align: left;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        width: 100%;
        text-align: center;
      }
      @media screen and (min-width: 768px) {
      }
      @media screen and (min-width: 1280px) {
      }
    }

    ul {
      display: flex;
      flex-wrap: nowrap;
      padding: 0;
      margin: 0;
      justify-content: space-between;
      @media screen and (min-width: 320px) and (max-width: 767px) {
        flex-wrap: wrap;
        flex-direction: column;
      }
      @media screen and (min-width: 768px) {
      }
      @media screen and (min-width: 1280px) {
      }

      li {
        width: 48%;
        margin: 0 20px 0 0;
        list-style-type: none;

        &:nth-last-child(2) {
          margin: 0;
        }
        @media screen and (min-width: 320px) and (max-width: 767px) {
          width: 100%;
          margin: 30px 0;
        }
        @media screen and (min-width: 768px) {
        }
        @media screen and (min-width: 1280px) {
        }

        h3 {
          width: auto;
          color: $main-text-color;
          font-size: 24px;
          line-height: 1.4;
          font-weight: 300;
          margin: 0 0 15px 0;
          padding-top: 30px;

          @media screen and (min-width: 320px) and (max-width: 767px) {
            width: 100%;
            text-align: center;
          }
          @media screen and (min-width: 768px) {
          }
          @media screen and (min-width: 1280px) {
          }
        }
      }
    }
  }
}
